/* chat */
.chat-answer {
    position: fixed;
    top: 60px;
    left: 0px;
    right: 0px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 60px);
    background: var(--main-glass-color);
    animation: animate-popDown .5s ease-in;
}

.chat-answer-content {
    max-height: calc(100% - 20px - 10px - 20px - 90px);
    background: var(--main-bg-color-2);
    border: 1px solid var(--primary-color);
    border-radius: var(--border-radius);
    padding: 10px;
    overflow: auto;
    font-size: var(--font-size-mid);
}

.chat-list {
    position: absolute;
    top: 60px;
    left: 15px;
    right: 15px;
    bottom: 120px;
    background: var(--main-bg-color-2);
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 15px;
    font-size: var(--font-size-mid);
    gap: 20px;
    border-radius: var(--border-radius);
    animation: animate-popUp .3s ease-in;
    overflow-y: auto;
}

.chat-msg-item-left {
    flex-shrink: 0;
    width: 100%;
    display: flex;
    justify-content: left;
}

.chat-msg-item-right {
    flex-shrink: 0;
    width: 100%;
    display: flex;
    justify-content: right;
}

.chat-msg {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    max-width: calc(100% - 30px - 2px);
    padding: 15px;
    border-radius: var(--border-radius);
    background: var(--main-bg-color-2);
    border: 1px solid var(--primary-color);
}

.chat-msg-item-right .chat-msg {
    max-width: calc(100% - 30px - 10px);
    background: var(--main-bg-color-2);
    border: 5px solid var(--primary-color);
}

.chat-msg-text {
    flex-direction: 0;
    width: calc(100% - 30px);
    height: 100%;
}

.chat-msg-operate-btn {
    flex-direction: 0;
    margin-left: 10px;
    width: 20px;
    height: 100%;
    font-size: var(--font-size-mid);
    cursor: pointer;
    color: var(--main-text-color-2);
}

.chat-msg-operate-btn.ok {
    color: var(--success-color);
}

/* feature */
.feature-list {
    width: calc(100% - 30px);
    padding: 15px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow: auto;
    padding-bottom: calc(80px + 90px);
}

.feature-item-row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.feature-mini-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: calc(100% - 5px);
    height: 100px;
    border-radius: var(--border-radius);
}

.feature-small-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: calc(50% - 5px);
    height: 100px;
    border-radius: var(--border-radius);
}

.feature-mid-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    height: 200px;
    border-radius: var(--border-radius);
}

.feature-larger-item {
    flex-shrink: 0;
    background: var(--main-bg-color-2);
    width: 100%;
    height: 300px;
    border-radius: var(--border-radius);
}

/* todo */
.todo {
    height: calc(100% - 20px);
    width: calc(100% - 20px);
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.todo-head {
    height: 35px;
    width: 100%;
    color: var(--primary-color);
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.todo-main {
    height: calc(100% - 35px);
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
    overflow: auto;
}

.todo-item {
    height: calc(100% - 10px);
    width: 65px;
    flex-shrink: 0;
    background: var(--primary-color);
    border-radius: var(--border-radius);
    display: flex;
    flex-direction: column;
    color: white;
    padding: 5px;
}

.todo-item-title {
    height: 65%;
    width: 100%;
    font-weight: bold;
    word-break: break-all;
    overflow: auto;
    text-align: center;
}

.todo-item-subtitle {
    height: 10%;
    width: 100%;
    font-size: var(--font-size-mini);
}

.todo-item-time {
    height: 25%;
    width: 100%;
    color: var(--warn-color);
}