.assistant-face-div {
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    padding: 5px;
}

.assistant-face {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
}

.assistant-face-logo {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}