.assistant {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.assistant-head {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: var(--max-zindex);
}

.assistant-main {
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0;
    right: 0;
    padding-top: 50px;
    padding-bottom: 90px;
}

.assistant-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 90px;
    background: var(--main-glass-color);
    backdrop-filter: var(--backdrop-filter);
}

.menu-bar {
    width: 100%;
    height: 100%;
    display: flex;
    color: var(--main-text-color-2);
}

.menu-item {
    width: 50%;
    height: 100%;
    cursor: pointer;
    font-size: var(--font-size-mid);
}